import React,{Component} from 'react';
import {
    BookImgShowWrapper
}from '../style'
class BookImgShow extends Component{
    render(){
        const { bookimg }=this.props;
        return(
            <BookImgShowWrapper>
                <img src={bookimg.mainsrc } className="big" alt="书籍大图展示"/>
                <ul className="listSmall">   
                    <li>
                        <img src={bookimg.src1 } className="imgsmall" alt="书籍小图展示"/>
                        
                    </li>
                    <li><img src={bookimg.src2 } className="imgsmall" alt="书籍小图展示"/></li>
                    <li><img src={bookimg.src3 } className="imgsmall" alt="书籍小图展示"/></li>
                </ul>
            </BookImgShowWrapper>
        )
    }
}
export default BookImgShow;