import React,{ Component, Fragment } from 'react';
import Button from '../Button/index'
import NavWrapper from '../Header/Nav';
import { connect } from 'react-redux';
import { actionCreators } from '../store/index'
import { 
    RegisterWrapper,
    RegisterTitle
} from './style'
import { ItemNew } from '../../pages/ManagePages/components/BookAlert/style';

//react Component 必须以大写字母开头
 class Regiter extends Component{
     constructor(props){
         super(props);
         this.state={
             phone:"",
             name:"",
             sex:"女",
             password:"",
             checkpwd:"",
             detailAddress:""
         }

     }
     handleChangeInput(e){
        const target = e.target;
        const value = target.name === 'sex' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
     }
    handleRegister(){
        const state=this.state;
        const user={
            name:state.name,
            phone:state.phone,
            sex:state.sex,
            password:state.password,
            checkpwd:state.checkpwd,
            detailAddress:state.detailAddress

        }
        console.log(user);
        this.props.register(user)

    }
    //使用react Hooks
    render(){
        const {loginState,loginOut,userInfo, isManage}=this.props;
        return(
            <Fragment>
                 <NavWrapper loginState={loginState} loginOut={loginOut} userInfo={userInfo} isManage={isManage}/>
            <RegisterWrapper>
               
                {/* style="margin-left:200px;" */}
                <form  >
                    {/* style="color:red;margin-left:170px;"  */}
                    <RegisterTitle>注册</RegisterTitle>
                    <span className="tips">$infos</span>
                    <ItemNew className="form-group">
                        <label  for="name">账号</label>  
                        {/* style="width:20%;"    */}
                        <input id="userid"  name="phone" class="form-control" placeholder="请输入手机号"/>
                        
                    </ItemNew>
                    <ItemNew className="form-group">
                        <label  for="name">昵称</label>     
                        <input id="username"  name="name" className="form-control" placeholder="请输入昵称"/>
                        
                    </ItemNew>
                    <ItemNew className="radioGroup">
                        <label for="name">性别</label>  
                        <input type="radio" value="1" name="sex" checked={this.state.sex==="男"?true:false}  className="inputRadio"/>男
                        <input type="radio" value="0" name="sex"  checked={this.state.sex==="女"?true:false} className="inputRadio" />女
                        <input type="text" style={{width:"34%",opacity:"0"}}/>
                    </ItemNew>
                    <ItemNew>
                        <label  for="name">密码</label>  
                        <input  type="text" id="password" name="password" className="form-control" placeholder="请输入密码"/>
                        
                    </ItemNew>
                    <ItemNew>
                        <label  for="name">确认密码</label>  
                        <input  type="text"  id="password"  name="checkpwd"  placeholder="请再次输入密码"/>
                    
                    </ItemNew>
                    <ItemNew className="detail">
                        <label for="name">地址</label>
      
                      
                  
                            <select className="form-control" name="provice" style={{width:"13%",marginRight:"10px"}}>
                            </select>
                       
                            <select className="form-control" name="city"  style={{width:"13%",marginRight:"10px"}}>
                            </select>
                            <select className="form-control" name="area"  style={{width:"13%"}}>
                            </select>
           
                    </ItemNew>
                
                    <ItemNew className=" detail">
                        <label for="name">详细地址</label>
                        <textarea className="form-control " rows="3" name="detailAddress"></textarea>
                    </ItemNew>
             
                    <Button className="success" onClick={this.handleRegister.bind(this)}>注册</Button>
    
                </form>
            </RegisterWrapper>
            </Fragment>
        )
    }
    
}
const mapState = (state) => ({
	loginState: state.getIn(['common', 'login']),
    userInfo:state.getIn(["common","userInfo"]),
    isManage:state.getIn(["common","isManage"])
})

const mapDispatch = (dispatch) => ({
    loginOut(){
        dispatch(actionCreators.logout());
    },
    register(user){
        dispatch(actionCreators.register(user))
    }
    
})

export default connect(mapState, mapDispatch)(Regiter);