import { fromJS } from 'immutable';
import * as constants from './constants';

const defaultState = fromJS({
	imgList: [],
	hotList:[],
	bookList:[],
	newBookList:[],

});

// const changeIndexData = (state, action) => {
// 	return state.merge({
// 		imgList: fromJS(action.imgList),
// 		hotList: fromJS(action.hotList),
// 		bookList:fromJS(action.bookList),
// 		newBookList:fromJS(action.newBookList)
// 	});
// };
export default (state = defaultState, action) => {
	switch(action.type) {
		case constants.GET_BOOK_LIST:
			return state.set("bookList",action.value)
		case constants.GET_NEW_LIST:
			return state.set("newBookList",action.value)
		case constants.GET_HOT_LIST:
			return state.set("hotList",action.value)
		case constants.GET_BANNER_LIST:
			return state.set("imgList",action.value)
		
		default:
			return state;
	}
}