import React,{ Component } from 'react';
import { Link } from 'react-router-dom';
import Button from '../../../../components/Button/index';
import { connect } from 'react-redux';
import { actionCreators } from '../../store/index';
import {
    MyOrderWrapper,
    MainSection,
    NavOrder,
    TitleOrder,
    TitleSheet,
    OrderItemTitle,
    MyOrderItem,
    OrderItemMain
} from './style'
class MyOrder extends Component{
    componentDidMount(){
        let user=this.props.location.state;
        console.log(this.props);
        if(user){
            this.props.getAllOrder(user)
        }
       
    }
    render(){
        const { order }=this.props;
        return(
            <MyOrderWrapper>

                <MainSection>
                    <NavOrder className="navOrder">
                        <TitleOrder className="titleOrd">全部订单</TitleOrder>
                        
                        <TitleSheet>
                            <span>书籍</span>
                            <span className="price">单价</span>
                            <span className="number">数量</span>
                            <span className="state">交易状态</span>
                        
                        </TitleSheet> 
                    </NavOrder>

                    <MyOrderItem  >
                        <OrderItemTitle className="secTitle">
                            <span className="date">时间:{order.creat_date }</span>
                            <span className="orderNumber">订单号：{order.oid }</span>

                        </OrderItemTitle>
                        <OrderItemMain class="secMain" key={order.oid}>
                            <img src="" alt="订单图片"/>
                            <span className="name">{order.goodName }</span>
                            <span className="price">￥{order.goodPrice }</span>
                            <span className="address">{order.detailAddress }</span>
                            <Link to={{
                                    pathname:"/book/commentBook",
                                    state:JSON.stringify(order) 
                            }} >
                                <Button className="primary">评价</Button>
                            </Link>
                        </OrderItemMain>
                        {
                            order.map((item)=>{
                                return(
                                    <OrderItemMain class="secMain" key={item.id}>
                                        <img src="" alt="订单图片"/>
                                        <span className="name">{item.goodName }</span>
                                        <span className="price">￥{item.goodPrice }</span>
                                        <span className="address">{item.detailAddress }</span>
                                        <Link to={{
                                                pathname:"/book/commentBook",
                                                state:JSON.stringify(item) 
                                        }} >
                                            <Button className="primary">评价</Button>
                                        </Link>
                                    </OrderItemMain>
                                 )  
                            })
                               
                        }
                        
                </MyOrderItem>

	

                </MainSection>

            </MyOrderWrapper>
        )
    }
}
const mapState=(state)=>({
    order:state.getIn(["person","allOrder"])
})
const mapDispatch = (dispatch) => ({
	getAllOrder(id){
		dispatch(actionCreators.getALLOrder(id));
	}
})
export default connect(mapState,mapDispatch)(MyOrder);