/* eslint-disable import/no-anonymous-default-export */
import { fromJS } from 'immutable';
import * as constants from './constants';

const defaultState = fromJS({
	user:[],
	usergusLike:[],
	userrecentOrder: [],
	allOrder:[]
});
const getPersonData = (state, action) => {
	return state.merge({
		user: fromJS(action.user),
		usergusLike: fromJS(action.usergusLike),
		userrecentOrder:fromJS(action.userrecentOrder),
	});
};
export default (state = defaultState, action) => {
	switch(action.type) {
		case constants.GET_PERSONDATA:
			return getPersonData(state,action)
		case constants.CHANGE_PERSON:
			return state.set("user",action.user)
		case constants.GET_ALL_ORDER:
			return state.set("allOrder",action.value)
		default:
			return state;
	}
}