import React,{Component} from 'react';
import { Link } from 'react-router-dom';
import {
    LeftSideWrapper,
    LeftLi
}from '../style';

class LeftSide extends  Component{
    render(){
        const {user}=this.props;
        return(
            <LeftSideWrapper>
                <div className="col-sm-2">
                <ul className="nav nav-pills nav-stacked">
            
                    <LeftLi className="active">
                        <a href="#">全部功能</a>
                    </LeftLi>
                    <LeftLi><Link to={{pathname:"/person",
                                        state:JSON.stringify(user)}}>我的资料</Link></LeftLi>
                    <LeftLi><Link to={{pathname:"/person/updateInfo",
                                        state:JSON.stringify(user)}}>修改个人资料</Link></LeftLi>
                    <LeftLi><Link to={{pathname:"/person/myOrder",
                                       state:JSON.stringify(user)}}
                                    >我的订单</Link></LeftLi>
                </ul>
                </div> 
            </LeftSideWrapper>
        )
    }
}
export default LeftSide;