import React,{Component} from 'react';
import BookImgShow from './components/BookImgShow';
import BookDetail from './components/BookDetail';
import BookMain from './components/BookMain';
import { connect } from 'react-redux';
import { actionCreators } from './store/index'
import {
    BookPageWrapper
}from './style'
class BookPage extends  Component{
    constructor(props){
        super(props);
        let book=this.props.location.state;
        if(book){
            book=JSON.parse(book);
        }
        
        console.log("state "+book)
        this.state={
            book:book||{},

        }

    }
    getDerivedStatrFromProps(nextProps,prevState){
        let book=this.props.location.state;
        if(book){
            book=JSON.parse(book);
        }
        
        this.setState({
            book:book||{}
        })

    }
    
    componentDidMount(){
        this.props.getBookInfo(this.state.book.id);
    }
    render(){
        const { book }=this.state;
        const { comment , bookimg }=this.props;
        console.log("book"+book)
        return(
            <BookPageWrapper>
                <BookImgShow bookimg={bookimg}/>
                <BookDetail book={book}/>
                <BookMain book={book} comment={comment}/>
            </BookPageWrapper>
        )
    }
}
const mapStateToProps=(state)=>({
    comment:state.getIn(["book","comment"]),
    book:state.getIn(["book","bookInfo"]),
    bookimg:state.getIn(["book","bookimg"]),

   

})
const mapDispatchToProps=(dispatch)=>({
    getBookInfo(bid){
       dispatch(actionCreators.getBookInfo(bid))
   }
})
export default connect(mapStateToProps,mapDispatchToProps)(BookPage);