import React,{ Component } from 'react';

import { connect } from 'react-redux';
import { actionCreators } from '../../store/index';
import {
    UpdateInfoWrapper,
    MainSection,
    UpdateTitle,
    UpdateForm,
    Button
}from './style'
class UpdateInfo extends Component{
    constructor(props){
        super(props)
        let user=this.props.location.state;
        console.log(this.props);
        if(user){
            user=JSON.parse(user);
        }
        
        this.state={
            user:user||{},

        }
    }
    handleChangeInput(e,type){
        switch(type){
            case 0:
                this.setState({
                    name:e.target.value
                });break;
            case 1:this.setState({
                    sex:e.target.value
                });break;
         

            default:return;
        }
        this.setState({
            name:e.target.name
        })

    }
    handleUpdateSubmit(){
        console.log(this.state.user)
        this.props.changePerson(this.state.user)
    }
    render(){
        const { user }=this.state;

        
        return(
            <UpdateInfoWrapper>
 
                <MainSection>
                    <UpdateTitle class="data">
                        <span>基本资料</span>
                    </UpdateTitle>
                    <UpdateForm  id="stuForm" >
                    
                        <div class="data1">
                            <img src="http://img7x1.ddimg.cn/imghead/15/9/9921910134921-1_o.png?=5792" alt="个人头像"/>
                            <span >{user.phone}</span>
                        </div>
                        <div class="form-group">
                            <label  for="name">昵称</label>     
                            <input id="username"  name="name" className="name" 
                                   value={user.name} onChange={this.handleChangeInput.bind(this,0)}/>
                                
                        </div>
                    
                        <div class="form-group">
                            <label for="name">性别</label>  
                            <div class="radio" >
                                <label class="radio-inline">
                                <input name="sex" type="radio" checked={user.sex==="男"?true:false}
                                                  onChange={this.handleChangeInput.bind(this,1)}/>男</label>
                            </div>
                            <div class="radio">
                                <label class="radio">
                                <input name="sex" type="radio" value="女" 
                                                checked={user.sex==="女"?true:false}
                                                onChange={this.handleChangeInput.bind(this,1)}/>女</label>
                            </div>
                        </div>
                        <div class="form-group">
                            <label for="name">地址</label>  
                            <div id="target">
                     
                                <select class="form-control" name="province">
                                
                                </select>
                            
                            <select class="form-control" name="city"></select>
                            
                                <select class="form-control" name="area" ></select>
                            </div>
                        </div>
                    <Button class="btn btn-primary" onClick={this.handleUpdateSubmit.bind(this)}>提交</Button>
                    </UpdateForm> 
                </MainSection>

            </UpdateInfoWrapper>
        )
    }
}
const mapDispatch = (dispatch) => ({
	changePerson(user){
		dispatch(actionCreators.updatePersonInfo(user));
	}
})
export default connect(null,mapDispatch)(UpdateInfo);;